// ========================================================================
// [slick] | 슬라이더
// ========================================================================
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-touch-callout: none;
  -khtml-user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  display: block;
  overflow: hidden;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-list,
.slick-slider .slick-track {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  top: 0;
  left: 0;
  // display: block;
  display: flex;
  justify-content: flex-start;
  margin-left: auto;
  margin-right: auto;
}

.slick-track:after,
.slick-track:before {
  display: table;
  content: '';
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  display: none;
  // float: left;
  height: 100%;
  min-height: 1px;
}

[dir='rtl'] .slick-slide {
  float: right;
}


.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}
.slick-arrow.slick-hidden {
  display: none;
}
.slider {
  width: 100%;
  margin: 40px auto;
}
.slick-list{
  margin: 0 60px;
}
.slick-dotted.slick-slider{
  margin-bottom: 70px;
}
.slick-slide {
  margin: 0 5px;
  ._mainBanner{
    height: 100%;
  }
}
.slick-slide img {
  width: 100%;
}
.slick-next:before,
.slick-prev:before {
  color: black;
}
.slick-slide {
  transition: all ease-in-out 0.3s;
  opacity: 0.2;
}
.slick-active {
  opacity: 1;
}
.slick-current {
  opacity: 1;
}
.slick-dots {
  position: absolute;
  bottom: -45px;
  list-style: none;
  display: block;
  text-align: center;
  padding: 0;
  margin: 0;
  width: 100%;
  li {
    position: relative;
    display: inline-block;
    height: 20px;
    width: 20px;
    line-height: 20px;
    margin: 0;
    padding: 0;
    text-align: center;
    cursor: pointer;
    button {
      border: 0;
      background: transparent;
      display: block;
      height: 100%;
      width: 100%;
      outline: none;
      line-height: 0;
      font-size: 0;
      color: transparent;
      padding: 5px;
      cursor: pointer;
      &:focus,
      &:hover {
        outline: none;
        &:before {
          background-color: #202020;
        }
      }
      &:before {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 11px;
        height: 11px;
        margin-top: -6px;
        margin-left: -6px;
        line-height: 20px;
        background-color: #d4d4d4;
        border-radius: 50%;
        opacity: 1;
        content: ''
      }
    }
    &.slick-active button:before {
      background-color: #202020;
      opacity: 1
    }
  }
}

/* Arrows */
.slick-next,
.slick-prev {
  position: absolute;
  display: block;
  height: 63px;
  width: 63px;
  line-height: 0;
  font-size: 0;
  cursor: pointer;
  border-radius: 50%;
  background-color: #fff;
  color: transparent;
  top: 50%;
  margin-top: -31px;
  -webkit-transform: translate(0, 0);
  -ms-transform: translate(0, 0);
  transform: translate(0, 0);
  padding: 0;
  border: none;
  outline: none;
  &:focus,
  &:hover {
    outline: none;
    background-color: #fff;
    color: transparent;
    &:before {
      opacity: $slick-opacity-on-hover;
    }
  }
  &.slick-disabled:before {
    opacity: $slick-opacity-not-active;
  }
  &:before {
    @include bg-img(13px, 22px, $imgNewPath, slide-i-arrow-prev);
    @extend %ir;
  }
}
.slick-prev {
  left: 0;
  margin-left: -20px;
  [dir="rtl"] & {
    left: auto;
    right: -25px;
  }
  &:before {
    margin-left: -4px;
    content: $slick-prev-character;
    [dir="rtl"] & {
      content: $slick-next-character;
    }
  }
}
.slick-next {
  right: 0;
  margin-right: -20px;
  [dir="rtl"] & {
    left: -25px;
    right: auto;
  }
  &:before {
    margin-right: -4px;
    transform: rotate(-180deg);
    content: $slick-next-character;
    [dir="rtl"] & {
      content: $slick-prev-character;
    }
  }
}
