// 상세
.detail-slider{
  .slick-slide{
    opacity: 1;
  }
  .slick-slide img {
    display: block;
    width: 341px;
    height: 207px
  }
  .thumb-img{
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-position: center
  }
  .video-area-1{
    position: relative;
    top: 0;
    left: 0;
    width: 341px;
    height: 207px;
    background-color: #000;
    cursor: pointer;
    video{
      width: 100%;
      height: auto;
      margin-top: 7px;
    }
  }
  .thumb-img-wrap{
    position: relative;
  }
  .thumb-img{
    position: relative;
    &::before{
      @include top-left(0, 0);
      display: block;
      width: 100%;
      height: 100%;
      background-color: #000;
      opacity: 0.3;
      content: '';
      z-index: 1;
    }
    &::after{
      @include top-left(50%, 50%);
      margin-top: -33px;
      margin-left: -33px;
      @include bg-img(67px, 67px, $imgNewPath, slide-btn-video);
      content: '';
      z-index: 2;
    }
    &.on{
      &::before, &::after{
        display: none
      }
    }
  }
  #background-video-1{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  #thumbImg-1 {
    background-image: url('#{$imgTempPath}/slide-1.jpg')
  }
}
