// 메인
.ui-container-box{
  position: relative;
  height: 420px;
  background-color: #f9f9f9
}
.ui-banner-box{
  margin: 0 auto;
  text-align: center;
  position: absolute;
  top: 0;
  margin-left: -960px;
  left: 50%;
  font-size: 0;
  line-height: 0;
}
.main-slider{
  display: inline-block;
  width: 1920px;
  height: 420px;
  overflow: hidden;
  &.slick-dotted.slick-slider{
    margin-bottom: 0;
  }
  .slick-list{
    margin: 0 auto;
    text-align: center;
  }
  .slick-slide {
    position: relative;
    margin: 0;
    .bg-img{
      display: block;
      width: 100%;
      height: 420px;
      text-align: left;
      &.device-pc{
        display: block;
      }
      &.device-mo{
        display: none;
      }
    }
  }
  .slider-wrap{
    @include top-left(0, 0);
    width: 100%;
    height: 100%;
    margin: 0 auto;
  }
  .slider-fit{
    position: relative;
    display: inline-block;
    width: 1280px;
    padding-top: 80px;
    padding-left: 134px;
    text-align: left;
    .slider-title{
      display: block;
      margin-bottom: 32px;
      font-size: 22px;
      letter-spacing: -0.05em;
    }
    .slider-info{
      display: block;
      margin-bottom: 30px;
      font-size: 44px;
      font-weight: $fw-bold;
      letter-spacing: -0.05em
    }
    .slider-btn{
      display: block;
      font-size: 17px;
      letter-spacing: -0.77px;
      .btn-detail-view{
        position: relative;
        padding: 0;
        padding-right: 30px;
        z-index: 10;
        &::after{
          @include top-right(3px, 0);
          @include bg-img(22px, 17px, $imgNewPath, main-slider-btn-view);
          content: ''
        }
      }
    }
    .slider-img{
      position: absolute;
      top: 0;
      right: 57px;
      img{
        width: 100%;
        height: auto;
      }
    }
  }
  .slick-dots-wrap{
    @include bottom-left(0, 0);
    width: 100%;
    // margin: 0 auto;
    // text-align: center;
  }
  .slick-dots{
    display: inline-block !important;
    bottom: 84px !important;
    left: 0;
    margin-left: 27.5% !important;
    text-align: left;
    li{
      width: 22px;
      button:before{
        width: 13px;
        height: 13px;
        background-color: #fff;
        opacity: 0.45
      }
      &.slick-active{
        button:before{
          opacity: 1
        }
      }
    }
  }
  .slick-prev{
    margin-left: 18%;
  }
  .slick-next{
    margin-right: 18%;
  }
  .slick-next, .slick-prev{
    background-color: transparent;
    z-index: 10;
    &::before{
      background-image: url("#{$imgNewPath}/main-slide-i-arrow-prev.png")
    }
    &::after{
      @include top-left(0, 0);
      width: 100%;
      height: 100%;
      background-color: #fff;
      border-radius: 50%;
      opacity: 0.15;
      content: ''
    }
  }
}
