.new-open-intro {
	position: relative;
	width: 100%;
	height: 100%;
	font-family: 'NanumSquareRound';
}

.new-open-intro::before {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-image: url("../../../img/intro/bg_error_m.png");
	background-size: cover;
	content: '';
}

.new-open-intro .ui-header-box {
	position: absolute;
	top: 0;
	left: 0;
	display: block;
	width: 100%;
	height: auto;
	margin: 0 auto;
	border-bottom: 0 none;
	text-align: center;
}

.new-open-intro .ui-header-box .ui-header-box-wrap {
	position: relative;
	display: inline-block;
	min-width: 1280px;
	height: auto;
	text-align: left;
}

.new-open-intro .ui-header-box .header-logo {
	position: absolute;
	top: 20px;
	left: 0;
	font-size: 0;
	z-index: 2;
}

.new-open-intro .ui-header-box .header-logo .img-logo {
	display: inline-block;
	width: 280px;
	height: 51px;
	background-image: url("../../../img/intro/eco-logo.png");
	background-position: 0 0;
	background-size: 280px 51px;
	background-repeat: no-repeat;
}

.new-open-intro .ui-header-box .header-logo a, .new-open-intro .ui-header-box .header-logo .h-title {
	display: inline-block;
	vertical-align: bottom;
}

.new-open-intro .ui-content-box {
	position: relative;
}

.new-open-intro .ui-content-box .sub-content.detail {
	display: flex;
	justify-content: space-around;
	align-items: center;
	height: 100%;
	padding: 0;
	letter-spacing: -.05em;
	border-radius: 0;
	box-shadow: none;
	background-color: transparent;
	margin: 0;
}

.new-open-intro .landing-wrap {
	text-align: center;
	line-height: 1.75;
	padding: 18rem 2rem;
}

.new-open-intro .landing-wrap .title {
	font-size: 2.5rem;
	font-weight: 800;
	line-height: 1.4;
	letter-spacing: 0;
	word-break: keep-all;
}

.new-open-intro .landing-wrap .desc {
	color: #333;
	font-size: 1.25rem;
	letter-spacing: 0;
	word-break: keep-all;
	margin: 2rem 0;
}

.new-open-intro .landing-wrap .desc-paragraph {
	display: block;
	margin-bottom: 20px;
}

.new-open-intro .landing-wrap .t-bold {
	font-weight: 700;
}

.new-open-intro .landing-wrap .p-block {
	display: block;
}

.new-open-intro .landing-wrap .desc a {
	font-weight: 800;
	border-bottom: 1px solid #333;
}

.new-open-intro .landing-wrap .m-br {
	display: inline-block;
}

.new-open-intro .landing-wrap .refer {
	color: #888;
	font-size: 1rem;
	letter-spacing: 0;
	word-break: keep-all;
}

.new-open-intro .landing-wrap .btn-comm {
	display: block;
	height: 2.917rem;
	line-height: calc(2.917rem - 2px);
	padding: 0 1.25rem;
	border: 1px solid #f37720;
	color: #fff;
	font-size: 1.083rem;
	letter-spacing: -.05em;
	text-align: center;
	background: #f37720;
}

.new-open-intro .landing-wrap .btn-comm.btn-md {
	width: 100%;
	font-weight: 600;
	letter-spacing: 0;
	border-radius: 10px;
	margin: 2rem auto 0;
}

@media all and (max-width: 1279px) {
	.new-open-intro .ui-header-box .ui-header-box-wrap {
		min-width: 100%;
	}
	.new-open-intro .ui-header-box .header-logo {
		width: 100%;
		margin: 0 auto;
		text-align: center;
	}
